html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  --error-red: #E57373;
}

body {
  margin: 0;
  height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: var(--surface-0);
  font-family: var(--font-family);
  font-weight: 400;
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.p-tabview-panels {
  padding: 0 !important;
}

.p-dropdown {
  width: 100%;
}

.p-sidebar-content {
  flex: 1;
  overflow: auto;
}

.p-dialog-content {
  overflow-y: unset !important;
}

.panel-header {
  text-align: center;
  font-size: 1.2rem;
  border-bottom: 1px solid var(--surface-d);
  padding-bottom: .5rem;
  margin-bottom: 1rem;
  width: 100%;
}

.p-dropdown.custom {
  background-color: unset;
  border: none;
  text-align: center;

  &.p-focus {
    box-shadow: none;
  }

  .p-dropdown-label {
    font-size: 1.5rem;
    color: var(--primary-color);
  }

  .p-dropdown-trigger {
    display: none;
  }
}

.pinned-custom.p-overlaypanel {
  .p-overlaypanel-content {
    padding: 1rem;

    .pinned-preset-overlay {
      width: 20rem;
    }

    .actions {
      margin-top: 1rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
}

.camera-management.p-sidebar {
  background: var(--surface-0);
  .p-sidebar-content {
    padding: 0;
  }
}

.p-button {
  &[disabled] {
    filter: brightness(.5);
  }
}

.p-dialog {
  .p-dialog-header .p-dialog-title {
    font-size: 1.5rem;
  }
}

.preset-management.p-overlaypanel {
  max-height: 30rem;
  overflow: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  display: flex;
  border-radius: .2rem;

  .p-overlaypanel-content {
    padding: 0;
    overflow: hidden;
    padding: .25rem;
    width: 100%;

    poc-preset-management {
      height: 100%;
    }
  }
}

.p-togglebutton.p-button.toggle-focus {
  .p-button-icon-left, .p-button-icon-right {
    color: var(--primary-color);
  }

  &.p-highlight {
    .p-button-icon-left, .p-button-icon-right {
      color: var(--primary-color-text);
    }
  }
}


.p-inputnumber.rangeInput {
  input {
    width: 100px;
    text-align: center;
  }
}

p-inputnumber {

  width: 100%;
  display: block;

  .p-inputnumber {
    width: 100%;

    input {
      text-align: center;
      min-width: 100px;
      width: 100%;
    };
  }
}

.core-css-plane{
  pointer-events: none;

  > svg {
    width: 100%;
    height: 100%;
    pointer-events: none;
    overflow: initial;

    g.axis-group {

      &.axis-width {
        transform: translateY(100%) translateX(50%);
      }

      &.axis-height {
        transform: translateY(50%);
      }

      path {
        display: none;
        stroke: green;
        stroke-width: .5px;
      }
      g.tick {
        line {
          stroke: lime;
          stroke-width: .25px;
        }

        text {
          display: none;
          stroke: white;
          fill: black;
          fill-opacity: .75;
          stroke-width: .1px;
          font-size: 10px;
          filter: drop-shadow(0 0px .5px rgba(0, 0, 0, .75));
        }
      }
    }
  }

  .dimension {
    position: absolute;
    color: lime;
    display: flex;
    align-items: center;
    justify-content: center;

    &.dimension-width {
      top: 100%;
      left: 0;
      right: 0;
    }

    &.dimension-height {
      top: 0;
      bottom: 0;
      left: -13px;
      > div {
        transform: translateX(-50%) rotate(
          -90deg
          );
      }
    }
  }
}

p-splitter {
  display: block;
  height: 100%;

  > .p-splitter {
    height: 100%;
  }
}
